/* Message content */


.message-body {}

.sender-details {
	padding: 20px 15px 0;
	border-bottom: $border-property;

	.pro-pic {
		max-width: 50px;
		margin-right: 15px;

		img {
			max-width: 100%;
			width: 100%;
			@include border-radius(100%);
		}
	}

	.details {
		padding-bottom: 0;

		.msg-subject {
			margin-bottom: 0;
			font-weight: 500;
		}

		.sender-email {
			margin-bottom: 20px;
			font-weight: 400;
			font-size: 14px;

			i {
				font-size: 16px;
				font-weight: 600;
				margin: 0 1px 0 7px;
			}

			a {
				color: $blue;
			}
		}
	}
}

.message-content {
	padding: 50px 15px;
}

.attachments-sections {
	ul {
		list-style: none;
		border-top: $border-property;
		padding: 30px 15px 20px;

		li {
			padding: 10px;
			margin-right: 20px;
			border: $border-property;
			@include border-radius(5px);
			@extend .d-inline-flex;
			@extend .flex-row;
			@extend .align-items-stretch;

			.thumb {
				display: inline-block;
				margin-right: 10px;
				@extend .d-flex;
				@extend .justify-content-center;
				@extend .align-items-center;

				i {
					font-size: 30px;
					margin: 0;
					color: $darkslategray;
				}
			}

			.details {
				p.file-name {
					display: block;
					@extend %ellipsor;
					margin-bottom: 0;
					color: $darkslategray;
				}

				.buttons {
					@extend .d-flex;
					@extend .align-items-center;

					.file-size {
						margin-right: 10px;
						margin-bottom: 0;
						font-size: 13px;
					}

					a {
						font-size: 13px;
						margin-right: 10px;

						&:last-child {
							margin-right: 0;
						}
					}
				}
			}
		}
	}
}